import { Injectable } from '@angular/core';
import { Router } from  "@angular/router";
import { auth } from  'firebase/app';
import { AngularFireAuth } from  "@angular/fire/auth";
import { User } from  'firebase';  
import { environment } from 'src/environments/environment';
import * as firebase from 'firebase/app'; 
import { GooglePlus } from '@ionic-native/google-plus/ngx';
import { LoadingController, Platform } from '@ionic/angular'; 

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  USER:firebase.User;
  loading: HTMLIonLoadingElement;

   constructor(public  afAuth:  AngularFireAuth, 
    private googlePlus: GooglePlus,
    private loadingController: LoadingController,
    private platform: Platform,
    public  router:  Router) {  
      this.loader();
    }

  async loader(){
    this.loading = await this.loadingController.create({ 
      message: 'Please wait...',
      duration: 5000
    }) 
  }


  async anonymousLogin() {
    let BU = this;
    return new Promise<User>((res,rej)=>{
      this.afAuth.signInAnonymously().then(()=>{
        this.afAuth.onAuthStateChanged(function(user) { 
          if (user) { 
            BU.USER = user;
            res(user)
          }
        }).catch(function(error) { 
          res(null)
        });
      }).catch(function(error) { 
        res(null)
      });
    })
  }

  async login(email: string, password: string,returnURL:string) { 
    await this.afAuth.signInWithEmailAndPassword(email, password).then(data=>{ 
      if(data){ 
        this.USER = data.user;
        this.router.navigate(['home']) 
      }
    })
  }

  async logout(){ 
    this.afAuth.signOut().then(()=>{
      this.USER = null;
      this.router.navigate(['guest']).then(()=>{
        //window.location.reload();
      });    
    }); 
  }


  async register(email:string, password:string){
    await this.afAuth.createUserWithEmailAndPassword(email, password)
    .then(async(result)=>{ 
        await this.sendEmailVerification();
    }).catch((error)=>{
      alert("Biçimsiz eposta formatı")
    })    
  }

  async sendEmailVerification() {
    let BU = this;
    return this.afAuth.currentUser.then(u => u.sendEmailVerification())
    .then(() => {
      alert("Doğrulama için eposta adresi gönderdik")
      BU.router.navigate(['/home']).then(()=>{
        //window.location.reload()
      });
    }).catch(()=>{
      alert("Hata! Doğrulama için eposta gönderemedik")
    })
  }



  async  loginWithGoogle(returnURL:string){
    this.loading.present();
    let BU = this
    await  this.afAuth.signInWithPopup(new auth.GoogleAuthProvider())
    .then(function(data) { 
      BU.USER = data.user; 
      BU.router.navigate([returnURL]).then(async ()=>{
        BU.loading.dismiss();
      });
    })
    .catch(function(error) { 
      alert(error.message)
    });
  } 


  async doNativeGoogleLogin(returnURL:string){
    await this.loading.present();
    let BU = this;
    this.googlePlus.login({
      'scopes': '', // optional - space-separated list of scopes, If not included or empty, defaults to `profile` and `email`.
      'webClientId': environment.googleWebClientId, // optional - clientId of your Web application from Credentials settings of your project - On Android, this MUST be included to get an idToken. On iOS, it is not required.
      'offline': true, // Optional, but requires the webClientId - if set to true the plugin will also return a serverAuthCode, which can be used to grant offline access to a non-Google server
      })
      .then( async (user) =>  {
        BU.USER = user;
        await this.loading.present(); 
        BU.router.navigate([returnURL]).then(async ()=>{
          await this.loading.dismiss();
        });
        await this.afAuth.signInWithCredential(
          firebase.auth.GoogleAuthProvider.credential(user.idToken)
        )
      }, err => {
        console.log(err);
        if(!this.platform.is('cordova')){
          alert("doNativeGoogleLogin hata")
        } 
    })
  }
}