import { Component, Inject, OnInit } from '@angular/core';

import { Platform } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { AuthService } from 'src/app/services/auth.service'
import { User } from 'firebase/app';
import { filter } from 'rxjs/operators';
import { UrlService } from 'src/app/services/url.service';
import { NavigationEnd, Router } from '@angular/router';
import { AppVersion } from '@ionic-native/app-version/ngx';
import { UserService } from './services/user.service';
import { ChannelService } from './services/channel.service';
import { MenuController } from '@ionic/angular';
import { AccountService } from './services/account.service';
import { Account } from './objects/account.model';


 
@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent implements OnInit {
  selectedIndex    = 0;
  uid:string       = null;
  isAnonymous      = false; 
  userLess         = false
  user  : User;
  previousUrl   : string = null;
  currentUrl    : string = null;
  versionNumber : string; 
  account: Account;

  public appPages = [
    {
      title: 'Anasayfa',
      url: '/home',
      icon: 'home'
    },
    {
      title: 'Aboneliklerim',
      url: '/my-ears',
      icon: 'thumbs-up'
    },
    {
      title: 'Kanallarım',
      url: '/my-channels',
      icon: 'megaphone'
    }
  ];
  
  constructor(
    private platform    : Platform,
    private splashScreen: SplashScreen,
    private statusBar   : StatusBar,
    public  authService : AuthService,
    private router      : Router,
    private userService : UserService,
    private appVersion  : AppVersion,
    private urlService  : UrlService,
    private menu        : MenuController,
    private accountService: AccountService,
  ){
    this.initializeApp();
  }

  initializeApp() {
    this.platform.ready().then(async () => {
      this.statusBar.styleDefault();
      this.statusBar.backgroundColorByHexString('#ffffff');
      this.splashScreen.hide();
      if(this.platform.is('cordova')){
        this.appVersion.getVersionNumber().then(v=>{
          this.versionNumber=v
        }) 
      }
    });
  }

  ngOnInit() {
    const path = window.location.pathname.split('/')[1]; 
    if (path !== undefined) {
      this.selectedIndex = this.appPages.findIndex(page => page.title.toLowerCase() === path.toLowerCase());
    } 
    this.getUser();
    this.setPreviousUrl()
  }

  setPreviousUrl(){
    this.router.events.pipe(
      filter((event) => event instanceof NavigationEnd)
    ).subscribe((event: NavigationEnd) => {
      this.previousUrl  = this.currentUrl;
      this.currentUrl   = event.url;
      this.urlService.setPreviousUrl(this.previousUrl);
    });
  }

  getUser(){  

    this.authService.afAuth.authState.subscribe( async user => {  
      if(user){  
          this.menu.enable(true);
          this.authService.USER = user;
          this.user         = user  
          this.uid          = this.user.uid; 
          this.isAnonymous  = this.user.isAnonymous; 
          this.userLess     = false; 
          this.getAccount();
        }else{ 
          this.menu.enable(false);
          this.user         = null;
          this.uid          = null
          this.userLess     = true; 
      }
    })

  }

  async getAccount(){
    const uid = this.authService.USER.uid;
    this.accountService.getAccount(uid).then(async (data)=>{
      if(data){  
        this.account = data;
      }
    })
  }


  goRoute(route:string){  
    this.router.navigate([route]);
  }

  logout(){ 
    this.menu.close();
    this.selectedIndex  = 101;
    this.authService.logout();
    this.account = null;
  }

}
