import { Injectable } from '@angular/core'; 
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreCollectionGroup, AngularFirestoreDocument, CollectionReference, DocumentData } from '@angular/fire/firestore';
import {Channel, Scret} from 'src/app/objects/channel.model'
import {MessageService} from 'src/app/services/message.service' 
import * as firebase from 'firebase'; 

import { HttpClient } from '@angular/common/http';
import { Observable, Subject, throwError } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';
import { waitForAsync } from '@angular/core/testing';
import { AuthService } from './auth.service';
import { CartItem, Order } from '../objects/cart.model';


@Injectable({
  providedIn: 'root'
})
export class ChannelService {

    constructor( private fs:AngularFirestore, 
               private httpClient:HttpClient,
               public authService:AuthService,
               private messageService:MessageService ) { }

  getUserChannels():AngularFirestoreCollectionGroup<Scret> { 
    if(!this.authService.USER?.uid){
      window.location.href="/home"
    };
    let uid = this.authService.USER.uid
    return this.fs.collectionGroup('screts', ref => ref.where("uid", "==", uid)); 
  }

  saveChannel(channel:Channel,scret:Scret){ 
    if(channel.id) {  
      let id = channel.id;
      delete  channel.id 
      this.fs.doc('channels' + '/' + id).update(channel).then(()=>{
        this.getUserChannels();
      })
      return this.fs.doc('channels' + '/' + id).collection('screts').doc('scret_fields').update(scret)
    }else{
      delete channel.id
      return this.fs.collection("channels").add({...channel})
      .then(function(docRef:any) {
        scret.channelDoc = docRef;
        docRef.collection('screts').doc('scret_fields').set({...scret})
      }.bind(this)) 
      .catch(function(error) {
          console.error("Kanal eklenirken bir hata oldu ", error.message);
      });
    }
  }

  deleteChannel(channel_id:string):Promise<void>{ 
    return this.fs.collection('channels').doc(channel_id).delete()
  }

  getChannelInfo(id:string):AngularFirestoreDocument<Channel>{ 
    return this.fs.collection('channels').doc(id)
  }

  removeChannelMessages(channel_id:string){ 
    this.messageService.removeAllMessegeOfChannel(channel_id).then(()=>{
      console.log("Mesajlar silindi")
    })
  }

  checkSubscriptionVerify(uid:string,channel_id:string):AngularFirestoreDocument<Channel>{
    return this.fs.collection('channels').doc(channel_id).collection('subscribers').doc(uid)
  }

  getSubscribers(channel_id:string):CollectionReference<DocumentData>{ 
    return this.fs.collection('channels').doc(channel_id).collection('subscribers').ref 
  }

  getOrders(cid:string):AngularFirestoreCollection<Order>{
    return this.fs.collection('channels').doc(cid).collection('orders')
  }
  
}
