import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreCollectionGroup, AngularFirestoreDocument } from '@angular/fire/firestore';
import { Channel } from '../objects/channel.model';
import * as firebase from 'firebase';
import { Subject } from 'rxjs';
import { ToastController } from '@ionic/angular';


@Injectable({
  providedIn: 'root'
})
export class UserService {

  //user_ears:Channel[]=[];
  user_ears: Subject<Channel[]> = new Subject<Channel[]>();
  ears:Channel[];

  constructor( private fs:AngularFirestore,private toastController:ToastController ) { }

  async subsicribeToChannel(USER:firebase.User,channel_id:string,password:string,confirmation_required:boolean){
    let user_to_channel = {
                            uid:USER.uid,
                            created_at:firebase.firestore.FieldValue.serverTimestamp(),
                            channelDoc:this.fs.collection('channels').doc(channel_id).ref,
                            password:password,
                            veryfied:confirmation_required?false:true,
                            displayName:USER.displayName,
                            email:USER.email,
                          }

    try {
      const r = await this.fs.collection('channels')
        .doc(channel_id)
        .collection('subscribers').doc(USER.uid)
        .set({ ...user_to_channel });
        const toast = await this.toastController.create({
          color: 'dark',
          message: confirmation_required?'Abonelik isteği gönderildi. Onay bekleniyor':'Abone oldunuz.',
          duration: 2000
        });
        toast.present();
      return true
    } catch (err) {
      const toast = await this.toastController.create({
        color: 'danger',
        message: "Abonelik işlemi sırasında bir hata oldu !",
        duration: 2000
      });
      toast.present();
      return false
    }
  }

  unsubscribeFromChannel(channel_id:string,subscribe_id:string):Promise<any>{ 
    return this.fs.collection('channels').doc(channel_id).collection('subscribers').doc(subscribe_id).delete()
  }

  checkChannelExistInUserEarsList(uid:string,channel_id:string):AngularFirestoreDocument<Channel>{
    return this.fs.collection('channels').doc(channel_id).collection('subscribers').doc(uid)
  }

  getUserEars(uid:string):AngularFirestoreCollectionGroup{ 
    return this.fs.collectionGroup('subscribers', ref => ref.where("uid", "==", uid));
  }
  
  
}
