import { Inject, Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument, DocumentData} from '@angular/fire/firestore';
import { Account } from 'src/app/objects/account.model'
import { HttpClient,HttpHeaders } from '@angular/common/http';  
import { Address } from 'src/app/objects/account.model';

@Injectable({
  providedIn: 'root'
})
export class AccountService {

  constructor(private fs:AngularFirestore,
              @Inject("rootURL") public rootURL:URL,
              private httpClient: HttpClient) { }

  async isExistTag(tag:string):Promise<boolean>{
    let exist:boolean = true
    return this.fs.collection('accounts').ref.where('tag','==',tag).get().then(snapshot => {
        exist = snapshot.docs.length?true:false
        return exist
      })
  }

  getAccount(uid:string):Promise<Account>{
    let a = this.fs.collection('accounts').doc(uid).ref.get()
    return a.then((acc:any) => {  
      return {...acc.data()}
    })    
  }

  

  getAddresses(uid:string):AngularFirestoreCollection<Address>{
    return this.fs.collection('accounts').doc(uid).collection('addresses')
  }

  saveAddress(uid:string,editedAddress:Address){
    if(!editedAddress.id){
      editedAddress.id = this.fs.createId()
    }
    return this.fs.collection('accounts').doc(uid).collection('addresses').doc(editedAddress.id).set({...editedAddress})
  }

  async saveAccount(account:Account){ 
    let headers = new HttpHeaders();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json' );
    headers.append('Access-Control-Allow-Origin','*');
    
    let options = {
      headers: headers
    } 
    //rootURL olacak
    let  url    = this.rootURL+"/SaveAccount"  
    return this.httpClient.post(url,account,options)
  }

  deleteAdress(uid:string,editedAddress:Address){
    return this.fs.collection('accounts').doc(uid).collection('addresses').doc(editedAddress.id).delete();
  }
  
  
}
