import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection,  AngularFirestoreDocument, DocumentData } from '@angular/fire/firestore'; 
import * as firebase from 'firebase';
import { FeedBack, Message } from 'src/app/objects/message.model'
import { AngularFireStorage } from '@angular/fire/storage';
import { AuthService } from './auth.service';
import { Brochure } from '../objects/brochure.model';
import { FoodDrinkMenu } from '../objects/food-drink-menu.model';

@Injectable({
  providedIn: 'root'
})
export class MessageService {

  public message:Message;

  constructor(  public fs:AngularFirestore,
                public authService:AuthService,
                private storage: AngularFireStorage) { } 

  save(message:Message,cid:string,doc_id:string){  
    return this.fs.collection('channels').doc(cid).collection('messages').doc(doc_id).set({...message})
  } 

  async saveMenu(message:Message,cid:string){ 
    const id            = message.id;
    delete message.id 
    let upload          = await this.uploadFiles(message,cid)
    if(!upload){
      console.log("Resimler Gönderilemedi")
    }
    message = JSON.parse(JSON.stringify(message))
    if(id){
      return this.fs.collection('channels')
                    .doc(cid)
                    .collection('messages')
                    .doc(id)
                    .update({...message}).then(()=>{
                      console.log("Menü Güncellendi")
                    })
    }else{
      message.created_at  = firebase.firestore.FieldValue.serverTimestamp(); 
      return this.fs.collection('channels')
                    .doc(cid)
                    .collection('messages')
                    .add({...message}).then(()=>{
                      console.log("Yeni Menü Kaydedildi")
                    })
    }
  }



  async saveBrochure(message:Message,cid:string){ 
    await this.uploadBroshureImages(message.brochure,cid)
    const id            = message.id;
    delete message.id 
    message = JSON.parse(JSON.stringify(message))
    if(id){
      return this.fs.collection('channels')
                    .doc(cid)
                    .collection('messages')
                    .doc(id)
                    .update({...message}).then((res)=>{
                      return res
                    })
    }else{
      message.created_at  = firebase.firestore.FieldValue.serverTimestamp(); 
      return this.fs.collection('channels')
                    .doc(cid)
                    .collection('messages')
                    .add({...message}).then((res)=>{
                      return res
                    })
    }
  }


  async saveFoodDrinkMenu(message:Message,cid:string){ 
    await this.uploadFoodDrinkMenuImages(message.food_drink_menu,cid)
    const id            = message.id;
    delete message.id 
    message = JSON.parse(JSON.stringify(message))
    if(id){
      return this.fs.collection('channels')
                    .doc(cid)
                    .collection('messages')
                    .doc(id)
                    .update({...message}).then((res)=>{
                      return res; 
                    })
    }else{
      message.created_at  = firebase.firestore.FieldValue.serverTimestamp(); 
      return this.fs.collection('channels')
                    .doc(cid)
                    .collection('messages')
                    .add({...message}).then((res)=>{
                      return res; 
                    })
    }
  }


  async uploadBroshureImages(broshure: Brochure, cid: string) {
    let prms:Promise<void>[]=[];
    broshure.categories.map((cat)=>{
      cat.pages.map((page)=>{
        page.images.map((img)=>{
          if(!img.File) return;

          let prm = new Promise<void>((res)=>{
            this.resizeImage(img.File).then((Blob)=>{
                let filePath        = firebase.auth().currentUser.uid +'/'+ cid + '/' + img.id;
                firebase.storage().ref(filePath).put(Blob).then((gsfile)=>{
                  gsfile.ref.getDownloadURL().then((url)=>{
                    img.File        = null;
                    img.base64data  = null;
                    img.url         = url; 
                    res();
                  })
                })
              })
            })
          prms.push(prm);

        })
      }) 
    })
    return await Promise.all(prms)
  }

  async uploadFoodDrinkMenuImages(foodDrinkMenu: FoodDrinkMenu, cid: string) {
    let prms:Promise<void>[]=[];
    foodDrinkMenu.categories.map((cat)=>{
      cat.foods.map((page)=>{
        page.images.map((img)=>{
          if(!img.File) return;

          let prm = new Promise<void>((res)=>{
            this.resizeImage(img.File).then((Blob)=>{
                let filePath        = firebase.auth().currentUser.uid +'/'+ cid + '/' + img.id;
                firebase.storage().ref(filePath).put(Blob).then((gsfile)=>{
                  gsfile.ref.getDownloadURL().then((url)=>{
                    img.File        = null;
                    img.base64data  = null;
                    img.url         = url; 
                    res();
                  })
                })
              })
            })
          prms.push(prm);

        })
      }) 
    })
    return await Promise.all(prms)
  }





  sendFeedBack(feedback:any,cid:string,message_id:string):Promise<void>{  
    let uid = (new Date()).getTime().toString()
    if(window.localStorage.getItem('anonim_uid')){
      uid = window.localStorage.getItem('anonim_uid')
    }else{
      window.localStorage.setItem('anonim_uid',uid)
    }
    
    if(firebase.auth().currentUser){
      uid = firebase.auth().currentUser.uid
    } 
    feedback.uid = uid;
    feedback = JSON.parse(JSON.stringify(feedback))
    return this.fs.collection('channels').doc(cid).collection('messages')
                  .doc(message_id).collection('feedbacks').doc(uid).set({...feedback})
  }

  deleteMessage(id:string,cid:string){ 
    return this.fs.collection('channels')
                  .doc(cid)
                  .collection('messages')
                  .doc(id).delete();
  }

  async removeAllMessegeOfChannel(cid:string){  

    return new Promise((resolve,reject)=>{
      this.fs.collection('channels').doc(cid).collection("messages").get().subscribe(m => {  
        m.forEach((a)=>{
          this.fs.collection('channels').doc(cid).collection("messages").doc(a.id).delete();
        })  
        console.log("removeAllMessegeOfChannel aboneliği tetiklendi");
        resolve(true)    
      });
    }).catch((err)=>{
        console.log("Mesajlar silinirken hata oldu : " + err)
    }) 

  }

  async uploadFiles(message:any,cid:string){
    let itemPrms      = new Promise<void>((res,rej)=>{
      let itemFiles   = []
      Object.keys(message.menu['items']).forEach(function(i){
          if(message.menu['items'][i].logoFiles){
            itemFiles[i]                  = message.menu['items'][i].logoFiles
            message.menu['items'][i].logo = '';
          }
          delete message.menu['items'][i].logoFiles 
      });
  
      if(Object.keys(itemFiles).length){
        for(let i in itemFiles){
          this.resizeImage(itemFiles[i][0]).then((Blob)=>{ 
            let filePath        = firebase.auth().currentUser.uid +'/'+ cid + '/' + i;
            firebase.storage().ref(filePath).put(Blob).then(()=>{
              res()
            })
          })
        }
      }else{
        res()
      }
    }).catch(()=>console.log("Categori resmi gönderilemedi"))

    let catPrms  = new Promise((res,rej)=>{
      let categoriFiles       = []
      Object.keys(message.menu['categories']).forEach(function(k){ 
        if(message.menu['categories'][k].logoFiles){
          categoriFiles[k]      = message.menu['categories'][k].logoFiles
          message.menu['categories'][k].logo = '';
        }
        delete message.menu['categories'][k].logoFiles
      });
  
      if(Object.keys(categoriFiles).length){
        for(let k in categoriFiles){
          this.resizeImage(categoriFiles[k][0]).then((Blob)=>{ 
            let filePath        = firebase.auth().currentUser.uid  +'/'+ cid + '/' + k;
            firebase.storage().ref(filePath).put(Blob).then(()=>{
              res(true)
            })
          })                      
        }
      }else{
        res(false)
      }
    }).catch(()=>console.log("İtem resmi gönderilemedi"))

    let all = new Promise(async(res,rej)=>{
      await catPrms.then(async()=>{
        await itemPrms.then(()=>{
          res(true)
        }).catch(()=>{
          rej(false);
        })
      }).catch(()=>{
        rej(false);
      })
    })

    return await all;
  }
 
  async resizeImage(file):Promise<Blob>{
    return <Blob> await new Promise((res,rej)=>{
      let resizedFile:string
      const reader    = new FileReader();
      reader.readAsDataURL(file);
      reader.onload   = (revent) => {
        const img = new Image()
        img.onload = function(oimg){
          let resizeCanvas = document.createElement("canvas");
          var ctx = resizeCanvas.getContext("2d");
          resizeCanvas.width = 512;
          resizeCanvas.height = img.height*(512/img.width);
          ctx.drawImage(img, 0, 0, resizeCanvas.width, resizeCanvas.height); 
          resizeCanvas.toBlob(function(blob){
            res(blob);
          }, 'image/jpeg', 0.95);          
        }
        img.src = <string>revent.target.result
      };
      reader.onerror  = error => {
        console.log("File resize edilemedi " + error)
        rej(error);
      };
    }).catch((e)=>{
      console.log("File resize edilemedi " + e)
    }) 
  }

  deleteFilesFromStorage(deleteFilesFromStorage: any[],cid){ 
    deleteFilesFromStorage.forEach(filesID => {
      let filePath        = firebase.auth().currentUser.uid +'/'+ cid + '/' + filesID;
      firebase.storage().ref(filePath).delete()
        .then(()=>console.log("Belge Silindi"))
        .catch((err)=>console.log("Belge silinemedi",err))
    });
  }

  getTextMessages(cid:string):AngularFirestoreCollection<Message> {
    return this.fs.collection('channels').doc(cid)
                  .collection('messages',ref => ref
                    .where("type","==","text")
                    .orderBy("created_at","desc")
                  )
  }

  getBrochureMessages(cid:string):AngularFirestoreCollection<Message> {
    return this.fs.collection('channels').doc(cid)
                  .collection('messages',ref => ref
                    .where("type","==","brochure")
                    .orderBy("created_at","desc")
                  )
  }

  getFoodDrinkMessages(cid:string):AngularFirestoreCollection<Message> {
    return this.fs.collection('channels').doc(cid)
                  .collection('messages',ref => ref
                    .where("type","==","food_drink_menu")
                    .orderBy("created_at","desc")
                  )
  }

  getUserFeedbackIfExist(cid:string,mid:string):AngularFirestoreDocument<FeedBack>{
    return this.fs.collection('channels').doc(cid).collection('messages').doc(mid).collection('feedbacks').doc(firebase.auth().currentUser.uid)
  }

  getAllMessages(cid:string):AngularFirestoreCollection<Message> {
    return this.fs.collection('channels').doc(cid)
                  .collection('messages')
  }

  getMenuMessage(cid:string){
    return this.fs.collection('channels').doc(cid)
                  .collection('messages',ref => ref
                  .where("type","==","menu")
                  .orderBy("created_at","desc")
                  .limit(1))
  }

  getMessageById(cid:string,mid:string):AngularFirestoreDocument{
    return this.fs.collection('channels').doc(cid).collection('messages').doc(mid)
  }

  getFeedBackByUID(mid:string,cid:string):Promise<DocumentData>{
    
    let uid = (new Date()).getTime().toString()
    if(window.localStorage.getItem('anonim_uid')){
      uid = window.localStorage.getItem('anonim_uid')
    }else{
      window.localStorage.setItem('anonim_uid',uid)
    }

    if(firebase.auth().currentUser){
      uid = firebase.auth().currentUser.uid
    }
    return this.fs.doc('/channels/'+cid+'/messages/'+mid+'/feedbacks/'+uid).ref.get()
  }

  getMessageStats(mid:string,cid:string):AngularFirestoreDocument{ 
    return this.fs.doc('/channels/'+cid+'/messages/'+mid+'/feedbacks/stats')
  }

  getMessageReplyes(mid:string,cid:string,start_after_doc:any,limit:number):AngularFirestoreCollection{  
    if(start_after_doc){
      return this.fs.collection('/channels/'+cid+'/messages/'+mid+'/feedbacks',
                                ref=> ref.startAfter(start_after_doc).limit(limit))
    }else{
      return this.fs.collection('/channels/'+cid+'/messages/'+mid+'/feedbacks',ref=> ref.limit(limit))
    }
  }
  
  
}